import { useSdk } from '@cetus/sdk-factory'
import { useAccountStore } from '@cetus/stores'
import useTokenBalanceStore from '@cetus/stores/src/tokenBalance'
import { extractStructTagFromType } from '@cetusprotocol/common-sdk'
import { CoinBalance } from '@mysten/sui/client'
import { useEffect, useRef } from 'react'

export function useAccountBalance() {
  const { currentAccount } = useAccountStore()
  const clmmSdk = useSdk('clmm')
  const { setTokenBalanceObj, clearData, setIsLoading, isLoading, ownerAddress } = useTokenBalanceStore()
  const currentAcc = useRef(currentAccount?.address)
  useEffect(() => {
    currentAcc.current = currentAccount?.address
  }, [currentAccount?.address])

  const fetchAccountBalance = async (account: string = currentAccount?.address || '', forceRefresh: boolean = false) => {
    try {
      if (isLoading && !forceRefresh) {
        return
      }

      if (account) {
        setIsLoading(true)
        const res = await clmmSdk!.FullClient.getOwnerCoinBalances(account)
        const obj: Record<string, CoinBalance> = {}
        res.forEach(coin => {
          obj[extractStructTagFromType(coin.coinType).full_address] = coin
        })
        if (account == currentAcc.current) {
          setTokenBalanceObj(obj, account)
        }
      }
    } catch (error) {
      console.log('ðŸš€ ~ file: useAccountBalance.ts:18 ~ fetchAccountBalance ~ error:', error)
      if (ownerAddress !== account) {
        clearData()
      }
    } finally {
      setIsLoading(false)
    }
  }

  const fetchCoinBalance = async (account: string = currentAccount?.address || '', coinType?: string) => {
    try {
      if (account && coinType) {
        const res = await clmmSdk!.FullClient.getOwnerCoinBalances(account, coinType)
        const obj: Record<string, CoinBalance> = {}

        res.forEach(coin => {
          obj[extractStructTagFromType(coin.coinType).full_address] = coin
        })
        if (account == currentAcc.current) {
          setTokenBalanceObj(obj, account)
        }
        return obj
      }
    } catch (error) {
      console.log('ðŸš€ ~ file: useAccountBalance.ts:18 ~ fetchCoinBalance ~ error:', error)
    }
  }

  useEffect(() => {
    if (!currentAccount) {
      clearData()
      setIsLoading(false)
    }
  }, [currentAccount?.address])

  return {
    fetchAccountBalance,
    fetchCoinBalance,
    isLoading
  }
}
